html {
  background: linear-gradient(90deg, rgba(163,247,236,0.9024859943977591) 0%, rgba(29,124,253,1) 50%, rgba(96,69,252,1) 100%);

}


ul {
  list-style: none;
  margin: 0;
  padding-left: 0px;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
