:root {
  --text-color1: azure;
  --text-color2: #f0b113;
  --text-color3: #1d8c85;
  --text-color4: #67abd5;
  --warning-color: #d48723;
  --shadow-color: #8b008b;
  --elem-color1: indigo;
  --copy-writes-color: #545454;
  --transition-time: 250ms;
  --cubic: cubic-bezier(.4, 0, .2, 1);
}

body {
  letter-spacing: .03em;
  color: var(--main-text-color);
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
}

html {
  background: linear-gradient(90deg, rgba(163, 247, 236, .9) 0%, #1d7cfd 50%, #6045fc 100%);
}

